html {
  box-sizing: border-box;
  font-size: 100%;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}

main {
  height: calc(100% - 50px);
  display: flex;
  flex-direction: column;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.bg-yellow {
  background-color: #ffcd00;
}

.title {
  font-weight: bold;
  font-size: 12px;
  line-height: 140.62%;
  color: #000000;
  margin: 0;
}

.text {
  font-weight: normal;
  font-size: 12px;
  line-height: 140.62%;
  color: #000000;
  margin: 0;
}

.sub-nav {
  width: 100%;
  padding: 13.5px 5px;
  text-align: center;
}

.wrapper {
  position: relative;
}
.wrapper--select {
  margin-right: 15px;
  display: flex;
  flex: 1 1 auto;
}

.section {
  height: 100%;
  position: relative;
  z-index: 0;
  overflow: auto;
}
.section--alert {
  height: auto;
  overflow: hidden;
}

.map-wrapper {
  height: 100%;
}

.map {
  height: 100%;
  width: 100%;
}

.form--float {
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  z-index: 1;
}
.form .form-group {
  display: flex;
  justify-content: space-between;
}
.form .form-group .wrapper .dropdown .btn--white .fa-map-marked-alt {
  margin-right: 9.5px;
}
.form .form-group .wrapper .dropdown .btn--white .fa-chevron-down {
  color: #a3a3a3;
}
.form .form-select {
  background-image: url("../dist/img/select-down.png");
  background-size: 17px 11px;
  border: 1px solid #d5d5d5;
  box-sizing: border-box;
  border-radius: 4px;
  font-weight: normal;
  font-size: 16px;
  line-height: 140.62%;
  color: #333333;
}
.form .form-select:focus {
  border: 2px solid #d5d5d5;
  box-shadow: none;
}

.card {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  padding: 10px;
  z-index: 1;
}
.card--bottom {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
}
.card--bottom.active .card-header .action--toggle .icon--toggle {
  transform: rotate(180deg);
}
.card--bottom.active .card-header .action--toggle .text--show {
  display: block;
}
.card--bottom.active .card-header .action--toggle .text--hide {
  display: none;
}
.card--bottom.active .card-body {
  display: none;
}
.card .card-header {
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 12px;
}
.card .card-header .action--toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.card .card-header .action--toggle .icon--toggle {
  margin-right: 10px;
  font-weight: bold;
  color: #525252;
  transform: rotate(0deg);
}
.card .card-header .action--toggle .text {
  color: #525252;
}
.card .card-header .action--toggle .text--show {
  display: none;
}
.card .card-header .action--toggle .text--hide {
  display: block;
}
.card .card-body {
  padding: 0;
  display: block;
  height: 100%;
}

.box {
  width: 20px;
  height: 20px;
  display: block;
  margin-right: 5px;
}
.box--tertiary {
  background-color: #fff0b3;
}
.box--secondary {
  background-color: #f7db87;
}
.box--primary {
  background-color: #ffcd00;
}

.list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 15px;
}
.list__item:last-child {
  margin: 0;
}
.list__item .wrapper--item {
  display: flex;
  align-items: center;
}

.checkbox-wrapper .label-checkbox .form-checkbox {
  display: none;
}
.checkbox-wrapper .label-checkbox .form-checkbox:checked ~ .custom-checkbox {
  background-color: #72a61b;
  border: 1px solid #72a61b;
}
.checkbox-wrapper .label-checkbox .form-checkbox:checked ~ .custom-checkbox .icon--check {
  display: block;
}
.checkbox-wrapper .label-checkbox .custom-checkbox {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  background-color: #ffffff;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 2px;
}
.checkbox-wrapper .label-checkbox .custom-checkbox .icon--check {
  font-size: 12px;
  font-weight: bold;
  color: #ffffff;
  display: none;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

.btn--white {
  background: #ffffff;
  border: 1px solid #d5d5d5;
  border-radius: 4px;
}
.btn:focus {
  box-shadow: none;
}